"use client";
import { useState } from "react";
import axios from "axios";
import Dice from "./components/Dice";

export default function DiceGame() {
  const [bet, setBet] = useState<number>(0);
  const [balance, setBalance] = useState<number>(1000);
  const [roll, setRoll] = useState<number | null>(null);
  const [result, setResult] = useState<string>("");

  const rollDice = async () => {
    if (bet <= 0 || bet > balance) {
      setResult("Invalid bet amount.");
      return;
    }

    try {
      const response = await axios.post("http://localhost:8080/api/roll-dice", { bet });
      const { roll, newBalance } = response.data;
      setRoll(roll);
      setBalance(newBalance);
      setResult(`You rolled a ${roll}. ${roll >= 4 ? "You won!" : "You lost!"}`);
    } catch (error) {
      setResult("Error rolling dice.");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-900 text-white">
      <h1 className="text-3xl font-bold mb-4">Provably Fair Dice Game</h1>
      <p className="mb-2">Balance: ${balance}</p>

      <Dice roll={roll} /> {/* Dice Component */}

      <input
        type="number"
        value={bet}
        onChange={(e) => setBet(Number(e.target.value))}
        className="p-2 text-black rounded mt-4"
        placeholder="Enter Bet Amount"
      />
      <button onClick={rollDice} className="px-4 py-2 bg-blue-500 rounded mt-4">
        Roll Dice
      </button>
      {result && <p className="mt-4">{result}</p>}
    </div>
  );
}
